}

.card-content p {
  font-size: 1.5rem;
  font-weight: 600;
}

.habits-section {
  margin-top: var(--spacing-xl);
}

.section-header {
  margin-bottom: var(--spacing-lg);
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.habits-filters {
  gap: var(--spacing-md);
  margin-bottom: var(--spacing-lg);
