  font-weight: 500;
}

.habit-actions {
  gap: var(--spacing-sm);
  margin-top: var(--spacing-md);
  display: flex;
}

.fab {
  right: var(--spacing-lg);
  border-radius: var(--radius-full);
  background-color: var(--secondary);
  color: #fff;
  width: 56px;
  height: 56px;
  box-shadow: var(--shadow-lg);
  cursor: pointer;
  transition: all var(--transition-normal);
  z-index: var(--z-fab);
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
}

.fab:hover {
  transform: scale(1.1);
